.SEO {
	.desktop {
		div {
			a {
				h3 {
					color: #1a0dab;
				}
				p {
					text-decoration: none;
					color: green;
				}
				//    &:hover{
				//        text-decoration: none;
				//    }
			}
		}
	}
	.mobile {
		.inner_mobile {
			border-radius: 5px;
			background: #fff;
			padding: 30px;
			box-shadow: 0 0.125rem 2.25rem rgba(0, 0, 0, 0.075) !important;
			a {
				h3 {
					color: #1a0dab;
				}
				p {
					text-decoration: none;
					color: green;
				}
			}
			.row {
				img {
					border-radius: 10px;
					width: 150px;
					height: 150px;
				}
			}
		}
	}
	.facebook {
		img {
			height: 300px;
		}
		.card-body {
			a {
				font-weight: 700;
				font-size: 24px;
				&:hover {
					text-decoration: none;
					color: black;
				}
			}
			.card-text {
				opacity: 0.7;
			}
		}
		&:hover {
			opacity: 0.9;
		}
	}
}
